import { defineStore } from 'pinia'
import { ref, computed } from 'vue'
import moment from 'moment'
import { Solar } from 'lunar-javascript'

export const useCalendarStore = defineStore('calendar', () => {
  // Áä∂ÊÄÅ
  const pageTitle = ref('ÊàëÁöÑÊó•ÂéÜ')
  const currentView = ref('month')
  const selectedDate = ref(moment())
  const events = ref([])
  const loading = ref(false)
  const debugInfo = ref('') // Ê∑ªÂä†Ë∞ÉËØï‰ø°ÊÅØ
  
  // È¢úËâ≤ÈÄâÈ°π
  const colorOptions = ref([
    '#2979ff', '#f56c6c', '#67c23a', '#e6a23c', 
    '#909399', '#ff85c0', '#5cdbd3', '#b37feb'
  ])

  // ÁéØÂ¢ÉÈÖçÁΩÆ
  const getBaseURL = () => {
    // #ifdef H5
    if (process.env.NODE_ENV === 'development') {
      return 'http://localhost:3000' // H5ÂºÄÂèëÁéØÂ¢É‰ΩøÁî®localhost
    } else {
      return window.location.origin // H5Áîü‰∫ßÁéØÂ¢É‰ΩøÁî®ÂΩìÂâçÂüüÂêç
    }
    // #endif
    // #ifdef MP-WEIXIN || APP-PLUS
    return 'https://oozy-moaningly-macy.ngrok-free.dev' // Â∞èÁ®ãÂ∫è‰ΩøÁî®ngrok
	 // https://oozy-moaningly-macy.ngrok-free.dev
    // #endif
    return 'https://oozy-moaningly-macy.ngrok-free.dev' // ÈªòËÆ§‰ΩøÁî®ngrok
  }

  // Ê£ÄÊµãÊòØÂê¶‰∏∫ ngrok ÁéØÂ¢É
  const isNgrokEnvironment = () => {
    const baseURL = getBaseURL()
    return baseURL.includes('ngrok-free.dev') || baseURL.includes('ngrok.io')
  }

  // Ëé∑ÂèñÂä®ÊÄÅËØ∑Ê±ÇÂ§¥
  const getRequestHeaders = () => {
    const headers = {
      'Content-Type': 'application/json'
    }
    
    // Âè™ÊúâÂú® ngrok ÁéØÂ¢ÉÊâçÊ∑ªÂä†Ë∑≥ËøáÈ™åËØÅÂ§¥ÈÉ®
    if (isNgrokEnvironment()) {
      headers['ngrok-skip-browser-warning'] = 'true'
      headers['X-Requested-With'] = 'XMLHttpRequest'
    }
    
    return headers
  }

  // ËÆ°ÁÆóÂ±ûÊÄßÔºà‰øùÊåÅ‰∏çÂèòÔºâ
  const displayDate = computed(() => {
    switch (currentView.value) {
      case 'month':
        return selectedDate.value.format('YYYYÂπ¥MMÊúà')
      case 'week':
        const startOfWeek = selectedDate.value.clone().startOf('week')
        const endOfWeek = selectedDate.value.clone().endOf('week')
        return `${startOfWeek.format('MMÊúàDDÊó•')} - ${endOfWeek.format('MMÊúàDDÊó•')}`
      case 'day':
        return selectedDate.value.format('YYYYÂπ¥MMÊúàDDÊó•')
      default:
        return selectedDate.value.format('YYYYÂπ¥MMÊúà')
    }
  })

const monthDays = computed(() => {
    const startDay = selectedDate.value.clone().startOf('month').startOf('week')
    const endDay = selectedDate.value.clone().endOf('month').endOf('week')
    const days = []
    let day = startDay.clone()
    
    while (day.isBefore(endDay, 'day') || day.isSame(endDay, 'day')) {
      // ÂÜúÂéÜËÆ°ÁÆóÈÄªËæë
      const solar = Solar.fromYmd(day.year(), day.month() + 1, day.date())
      const lunar = solar.getLunar()
      let lunarText = lunar.getDayInChinese()
      if (lunar.getDay() === 1) lunarText = lunar.getMonthInChinese() + 'Êúà'
      const festival = lunar.getFestivals()[0] || lunar.getOtherFestivals()[0]

      days.push({
        date: day.clone(),
        day: day.date(),
        lunarDay: festival || lunarText, // ÂÜúÂéÜÊàñËäÇÊó•
        isCurrentMonth: day.isSame(selectedDate.value, 'month'),
        isToday: day.isSame(moment(), 'day'),
        isSelected: day.isSame(selectedDate.value, 'day')
      })
      day.add(1, 'day')
    }
    return days
  })

  const weekDays = computed(() => {
    const startOfWeek = selectedDate.value.clone().startOf('week')
    const days = []
    
    for (let i = 0; i < 7; i++) {
      const day = startOfWeek.clone().add(i, 'days')
      days.push({
        fullDate: day,
        weekday: ['Êó•', '‰∏Ä', '‰∫å', '‰∏â', 'Âõõ', '‰∫î', 'ÂÖ≠'][i],
        date: day.date()
      })
    }
    
    return days
  })

  // ÊñπÊ≥ïÔºà‰øùÊåÅ‰∏çÂèòÔºâ
  const switchView = (view) => {
    currentView.value = view
  }

  const previousPeriod = () => {
    switch (currentView.value) {
      case 'month':
        selectedDate.value = selectedDate.value.clone().subtract(1, 'month') 
        break
      case 'week':
        selectedDate.value = selectedDate.value.clone().subtract(1, 'week')
        break
      case 'day':
        selectedDate.value = selectedDate.value.clone().subtract(1, 'day')
        break
    }
    loadEvents()
  }

  const nextPeriod = () => {
    switch (currentView.value) {
      case 'month':
        selectedDate.value = selectedDate.value.clone().add(1, 'month')
        break
      case 'week':
        selectedDate.value = selectedDate.value.clone().add(1, 'week')
        break
      case 'day':
        selectedDate.value = selectedDate.value.clone().add(1, 'day')
        break
    }
    loadEvents()
  }

  const goToToday = () => {
    selectedDate.value = moment()
    loadEvents()
  }

  const selectDate = (date) => {
    selectedDate.value = date.clone()
    // if (currentView.value === 'month') {
    //   currentView.value = 'day'
    // }
    loadEvents()
  }

  // ‰∫ã‰ª∂Áõ∏ÂÖ≥ÊñπÊ≥ïÔºà‰øùÊåÅ‰∏çÂèòÔºâ
  const getTimeEventsForDay = (date) => {
    const dateStr = date.format('YYYY-MM-DD')
    return events.value.filter(event => 
      event.startDate === dateStr || 
      event.endDate === dateStr ||
      (event.startDate <= dateStr && event.endDate >= dateStr)
    )
  }

  const getEventsForDayAndTime = (date, time) => {
    const dateStr = date.format('YYYY-MM-DD')
    return events.value.filter(event => {
      const dateMatch = event.startDate === dateStr || 
                       event.endDate === dateStr ||
                       (event.startDate <= dateStr && event.endDate >= dateStr)
      
      if (!dateMatch) return false
      
      if (event.startTime && event.endTime) {
        return time >= event.startTime && time < event.endTime
      }
      
      return false
    })
  }

  // Áªü‰∏ÄÂ§ÑÁêÜ uni.request ÂìçÂ∫î
  const handleUniResponse = (response) => {
    let statusCode, responseData
    
    // H5 ÁéØÂ¢É
    // #ifdef H5
    statusCode = response.status
    responseData = response.data
    // #endif
    
    // ÂæÆ‰ø°Â∞èÁ®ãÂ∫èÂíåAppÁéØÂ¢É
    // #ifdef MP-WEIXIN || APP-PLUS
    statusCode = response.statusCode
    responseData = response.data
    // #endif
    
    // ÂÖºÂÆπÊÄßÂ§ÑÁêÜ
    if (statusCode === undefined) {
      statusCode = response.statusCode || response.status
      responseData = response.data || response
    }
    
    return { statusCode, responseData }
  }

  // ==================== Ë∞ÉËØïÊñπÊ≥ï ====================
  
  // ÂÆåÊï¥ÁöÑÁ≥ªÁªüË∞ÉËØï
  const debugSystem = async () => {
    const debugLog = []
    const baseURL = getBaseURL()
    
    debugLog.push('üöÄ ===== ÂºÄÂßãÁ≥ªÁªüË∞ÉËØï =====')
    debugLog.push(`üìç Âü∫Á°ÄURL: ${baseURL}`)
    debugLog.push(`üïê Ë∞ÉËØïÊó∂Èó¥: ${new Date().toLocaleString()}`)
    debugLog.push(`üåê ÂΩìÂâçÁéØÂ¢É: ${isNgrokEnvironment() ? 'NgrokÁéØÂ¢É' : 'Êú¨Âú∞ÁéØÂ¢É'}`)
    
    // #ifdef MP-WEIXIN
    debugLog.push('üì± ËøêË°åÁéØÂ¢É: ÂæÆ‰ø°Â∞èÁ®ãÂ∫è')
    // #endif
    // #ifdef H5
    debugLog.push('üåê ËøêË°åÁéØÂ¢É: H5ÁΩëÈ°µ')
    // #endif
    
    try {
      // 1. ÊµãËØïÂÅ•Â∫∑Ê£ÄÊü•
      debugLog.push('\nüîó ÊµãËØï1: ÂÅ•Â∫∑Ê£ÄÊü•Êé•Âè£')
      const healthResult = await testHealth()
      debugLog.push(`‚úÖ ÂÅ•Â∫∑Ê£ÄÊü•: ${healthResult.success ? 'ÊàêÂäü' : 'Â§±Ë¥•'}`)
      debugLog.push(`üìä ÂìçÂ∫îÊï∞ÊçÆ: ${JSON.stringify(healthResult.data)}`)
      
      // 2. ÊµãËØïÁΩëÁªúËøûÊé•
      debugLog.push('\nüåê ÊµãËØï2: ÁΩëÁªúËøûÊé•ÊµãËØï')
      const networkResult = await testNetwork()
      debugLog.push(`üì∂ ÁΩëÁªúÁ±ªÂûã: ${networkResult.networkType}`)
      
      // 3. ÊµãËØï‰∫ã‰ª∂API
      debugLog.push('\nüìÖ ÊµãËØï3: ‰∫ã‰ª∂APIÊµãËØï')
      const eventsResult = await testEventsAPI()
      debugLog.push(`üì° APIÁä∂ÊÄÅÁ†Å: ${eventsResult.statusCode}`)
      debugLog.push(`üì¶ ÂéüÂßãÊï∞ÊçÆÊ†ºÂºè: ${typeof eventsResult.responseData}`)
      debugLog.push(`üî¢ Ëß£ÊûêÂêé‰∫ã‰ª∂Êï∞Èáè: ${eventsResult.parsedData.length}`)
      
      // 4. ÊµãËØïÊï∞ÊçÆÊ†ºÂºè
      debugLog.push('\nüîç ÊµãËØï4: Êï∞ÊçÆÊ†ºÂºèÂàÜÊûê')
      if (eventsResult.responseData) {
        debugLog.push(`üìä ÂìçÂ∫îÊï∞ÊçÆKeys: ${Object.keys(eventsResult.responseData).join(', ')}`)
        if (Array.isArray(eventsResult.responseData)) {
          debugLog.push('‚úÖ Êï∞ÊçÆÊ†ºÂºè: Áõ¥Êé•Êï∞ÁªÑ')
        } else if (eventsResult.responseData.data) {
          debugLog.push('‚úÖ Êï∞ÊçÆÊ†ºÂºè: ÂåÖÂê´dataÂ≠óÊÆµÁöÑÂØπË±°')
        } else if (eventsResult.responseData.events) {
          debugLog.push('‚úÖ Êï∞ÊçÆÊ†ºÂºè: ÂåÖÂê´eventsÂ≠óÊÆµÁöÑÂØπË±°')
        } else {
          debugLog.push('‚ùì Êï∞ÊçÆÊ†ºÂºè: Êú™Áü•Ê†ºÂºè')
        }
      }
      
      debugLog.push('\nüéØ ===== Ë∞ÉËØïÂÆåÊàê =====')
      
    } catch (error) {
      debugLog.push(`‚ùå Ë∞ÉËØïËøáÁ®ã‰∏≠Âá∫Èîô: ${error.message}`)
    }
    
    // ÊâìÂç∞Âà∞ÊéßÂà∂Âè∞
    console.log(debugLog.join('\n'))
    debugInfo.value = debugLog.join('\n')
    
    return debugLog
  }

  // ÂÅ•Â∫∑Ê£ÄÊü•ÊµãËØï
  const testHealth = async () => {
    try {
      const baseURL = getBaseURL()
      const url = baseURL + '/health'
      
      const response = await new Promise((resolve, reject) => {
        uni.request({
          url,
          method: 'GET',
          timeout: 10000,
          header: getRequestHeaders(), // ‰ΩøÁî®Âä®ÊÄÅÂ§¥ÈÉ®
          success: (res) => resolve(res),
          fail: (err) => reject(err)
        })
      })
      
      const { statusCode, responseData } = handleUniResponse(response)
      
      return {
        success: statusCode === 200,
        statusCode,
        data: responseData
      }
    } catch (error) {
      return {
        success: false,
        error: error.message
      }
    }
  }

  // ÁΩëÁªúËøûÊé•ÊµãËØï
  const testNetwork = async () => {
    return new Promise((resolve) => {
      uni.getNetworkType({
        success: (res) => {
          resolve({
            success: true,
            networkType: res.networkType
          })
        },
        fail: (err) => {
          resolve({
            success: false,
            error: err.errMsg
          })
        }
      })
    })
  }

  // ‰∫ã‰ª∂APIÊµãËØï
  const testEventsAPI = async () => {
    try {
      const baseURL = getBaseURL()
      const url = baseURL + '/api/events?userId=default-user'
      
      const response = await new Promise((resolve, reject) => {
        uni.request({
          url,
          method: 'GET',
          timeout: 15000,
          header: getRequestHeaders(), // ‰ΩøÁî®Âä®ÊÄÅÂ§¥ÈÉ®
          success: (res) => resolve(res),
          fail: (err) => reject(err)
        })
      })
      
      const { statusCode, responseData } = handleUniResponse(response)
      
      // Ëß£Êûê‰∫ã‰ª∂Êï∞ÊçÆ
      let parsedData = []
      if (Array.isArray(responseData)) {
        parsedData = responseData
      } else if (responseData && Array.isArray(responseData.data)) {
        parsedData = responseData.data
      } else if (responseData && Array.isArray(responseData.events)) {
        parsedData = responseData.events
      }
      
      return {
        statusCode,
        responseData,
        parsedData,
        parsedCount: parsedData.length
      }
    } catch (error) {
      return {
        error: error.message
      }
    }
  }

  // ==================== ‰∏ªË¶ÅAPIÊñπÊ≥ï ====================

  const loadEvents = async () => {
    try {
      loading.value = true
      
      const baseURL = getBaseURL()
      const url = baseURL + '/api/events?userId=default-user'
      
      console.log('üåê ËØ∑Ê±ÇÊó•Á®ãÊï∞ÊçÆ:', url)
      console.log('üìã ËØ∑Ê±ÇÂ§¥:', getRequestHeaders())
      console.log('üåç ÂΩìÂâçÁéØÂ¢É:', isNgrokEnvironment() ? 'Ngrok' : 'Êú¨Âú∞')
      
      const response = await new Promise((resolve, reject) => {
        uni.request({
          url,
          method: 'GET',
          timeout: 30000,
          header: getRequestHeaders(), // ‰ΩøÁî®Âä®ÊÄÅÂ§¥ÈÉ®
          success: (res) => resolve(res),
          fail: (err) => reject(err)
        })
      })
      
      // Ê£ÄÊü•ÂìçÂ∫îÂÜÖÂÆπÁ±ªÂûã
      const contentType = response.header && response.header['Content-Type'];
      if (contentType && contentType.includes('text/html')) {
        throw new Error('ÊúçÂä°Âô®ËøîÂõû‰∫ÜHTMLÈ°µÈù¢ËÄå‰∏çÊòØJSONÊï∞ÊçÆÔºåËØ∑Ê£ÄÊü•ngrokÈÖçÁΩÆ');
      }
      
      const { statusCode, responseData } = handleUniResponse(response)
      
      console.log('üì° ÂìçÂ∫îÁä∂ÊÄÅ:', statusCode)
      console.log('üì¶ ÂìçÂ∫îÊï∞ÊçÆ:', responseData)
      
      if (statusCode === 200) {
        // ÁÆÄÂåñÁöÑÊï∞ÊçÆËß£Êûê
        if (Array.isArray(responseData)) {
          events.value = responseData
        } else if (responseData && Array.isArray(responseData.data)) {
          events.value = responseData.data
        } else if (responseData && Array.isArray(responseData.events)) {
          events.value = responseData.events
        } else {
          console.warn('‚ö†Ô∏è Êó†Ê≥ïËØÜÂà´ÁöÑÊï∞ÊçÆÊ†ºÂºè')
          events.value = []
        }
        
        console.log(`‚úÖ ÊàêÂäüÂä†ËΩΩ ${events.value.length} ‰∏™Êó•Á®ã`)
      } else {
        throw new Error(`HTTPÈîôËØØ: ${statusCode}`)
      }
    } catch (error) {
      console.error('‚ùå Âä†ËΩΩ‰∫ã‰ª∂Â§±Ë¥•:', error)
      uni.showToast({
        title: 'Âä†ËΩΩÂ§±Ë¥•: ' + error.message,
        icon: 'none',
        duration: 4000
      })
      events.value = []
    } finally {
      loading.value = false
    }
  }

  const createEvent = async (eventData) => {
    try {
      const baseURL = getBaseURL()
      const url = baseURL + '/api/events'
      
      const response = await new Promise((resolve, reject) => {
        uni.request({
          url,
          method: 'POST',
          data: {
            ...eventData,
            userId: 'default-user'
          },
          header: getRequestHeaders(), // ‰ΩøÁî®Âä®ÊÄÅÂ§¥ÈÉ®
          timeout: 10000,
          success: (res) => resolve(res),
          fail: (err) => reject(err)
        })
      })
      
      const { statusCode, responseData } = handleUniResponse(response)
      
      if (statusCode === 200 || statusCode === 201) {
        if (responseData) {
          await loadEvents()
          return responseData.data || responseData
        } else {
          throw new Error('ÂàõÂª∫Êó•Á®ãÂ§±Ë¥•: ÂìçÂ∫îÊï∞ÊçÆ‰∏∫Á©∫')
        }
      } else {
        throw new Error(`HTTPÈîôËØØ: ${statusCode}`)
      }
    } catch (error) {
      console.error('‚ùå ÂàõÂª∫‰∫ã‰ª∂Â§±Ë¥•:', error)
      throw error
    }
  }

  const updateEvent = async (eventId, eventData) => {
    try {
      const baseURL = getBaseURL()
      const url = baseURL + `/api/events/${eventId}`
      
      const response = await new Promise((resolve, reject) => {
        uni.request({
          url,
          method: 'PUT',
          data: eventData,
          header: getRequestHeaders(), // ‰ΩøÁî®Âä®ÊÄÅÂ§¥ÈÉ®
          timeout: 10000,
          success: (res) => resolve(res),
          fail: (err) => reject(err)
        })
      })
      
      const { statusCode, responseData } = handleUniResponse(response)
      
      if (statusCode === 200) {
        if (responseData) {
          await loadEvents()
          return responseData.data || responseData
        } else {
          throw new Error('Êõ¥Êñ∞Êó•Á®ãÂ§±Ë¥•: ÂìçÂ∫îÊï∞ÊçÆ‰∏∫Á©∫')
        }
      } else {
        throw new Error(`HTTPÈîôËØØ: ${statusCode}`)
      }
    } catch (error) {
      console.error('‚ùå Êõ¥Êñ∞‰∫ã‰ª∂Â§±Ë¥•:', error)
      throw error
    }
  }

  const deleteEvent = async (eventId) => {
    try {
      const baseURL = getBaseURL()
      const url = baseURL + `/api/events/${eventId}`
      
      const response = await new Promise((resolve, reject) => {
        uni.request({
          url,
          method: 'DELETE',
          header: getRequestHeaders(), // ‰ΩøÁî®Âä®ÊÄÅÂ§¥ÈÉ®
          timeout: 10000,
          success: (res) => resolve(res),
          fail: (err) => reject(err)
        })
      })
      
      const { statusCode, responseData } = handleUniResponse(response)
      
      if (statusCode === 200) {
        if (responseData) {
          await loadEvents()
        } else {
          throw new Error('Âà†Èô§Êó•Á®ãÂ§±Ë¥•: ÂìçÂ∫îÊï∞ÊçÆ‰∏∫Á©∫')
        }
      } else {
        throw new Error(`HTTPÈîôËØØ: ${statusCode}`)
      }
    } catch (error) {
      console.error('‚ùå Âà†Èô§‰∫ã‰ª∂Â§±Ë¥•:', error)
      throw error
    }
  }

  return {
    // Áä∂ÊÄÅ
    pageTitle,
    currentView,
    selectedDate,
    events,
    loading,
    colorOptions,
    debugInfo,
    
    // ËÆ°ÁÆóÂ±ûÊÄß
    displayDate,
    monthDays,
    weekDays,
    
    // ÊñπÊ≥ï
    switchView,
    previousPeriod,
    nextPeriod,
    goToToday,
    selectDate,
    getTimeEventsForDay,
    getEventsForDayAndTime,
    loadEvents,
    createEvent,
    updateEvent,
    deleteEvent,
    
    // Ë∞ÉËØïÊñπÊ≥ï
    debugSystem,
    testHealth,
    testNetwork,
    testEventsAPI,
    
    // Â∑•ÂÖ∑ÊñπÊ≥ï
    isNgrokEnvironment,
    getRequestHeaders
  }
})